import React from 'react';

import { Server } from './server.js';

import freebuildImg from './img/Grass_Block_Revision_6.png';
import survivalImg from './img/sword.png';

export class ServerList extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      servers: [
        {
          name: 'Freebuild',
          ip: 'fb.ogCraft.pl',
          icon: freebuildImg,
          disabled: true,
          description: "W trakcie budowy",
        },
        {
          name: 'Survival',
          ip: 'sv.ogCraft.pl',
          icon: survivalImg,
        },
      ]
    };

    this.render = this.render.bind(this);
  }

  render() {
    return (
      <div className="col-md-12 server-list">
        {
          this.state.servers.map(
            (s) => <Server
              key={s.ip}
              name={s.name}
              ip={s.ip}
              icon={s.icon}
              disabled={s.disabled}
              description={s.description}
            />
          )
        }
      </div>
    )
  }
}